export default function Dashboard() {
  return (
    <div>
      <h1 className="text-2xl font-bold mb-4">Supplier Dashboard</h1>
      <div className="grid md:grid-cols-3 gap-6">
        <Card title="Total Deliveries" value="54" color="bg-teal-600" />
        <Card title="Pending Invoices" value="3" color="bg-yellow-600" />
        <Card title="Stock Supplied" value="12,500L" color="bg-blue-600" />
      </div>
    </div>
  )
}

function Card({ title, value, color }: { title: string; value: string; color: string }) {
  return (
    <div
      className={`p-6 rounded-2xl text-white shadow-lg ${color}`}
      tabIndex={0}
      aria-label={title}
    >
      <h2 className="text-lg font-semibold">{title}</h2>
      <p className="text-3xl mt-2">{value}</p>
    </div>
  )
}
